.image {
  cursor: pointer;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #f3f3f3;
  /* for upload form's common styles. */
  /* for before upload image. */
  /* for after upload image. */
}
.image .image-upload-form {
    position: relative;
    border-width: 1px;
    border-style: dashed;
    border-color: #c4c9d0;
    background-color: white;
    height: 350px;
}
.image .image-upload-form .upload-icon {
      width: 100px;
}
.image .image-upload-form p {
      margin: 0;
}
.image .image-upload-form:not(.image-uploaded) {
    transition: 0.3s;
}
.image .image-upload-form:not(.image-uploaded):hover {
      transition: 0.3s;
      background-color: #ebedef;
}
.image .image-upload-form.image-uploaded {
    opacity: 0;
    transition: 0.3s;
}
.image .image-upload-form.image-uploaded:hover {
      transition: 0.3s;
      opacity: 0.6;
}
.image .image-upload-form.dragging {
    transition: 0s;
    border-color: #39f;
    border-width: 1px;
    background-color: #ebedef;
}
.image .image-upload-form.dragging:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 10000;
}
.image .drag-over-icon {
    width: 100px;
    height: 100px;
}
