.header-logo {
  color: #20a8d8;
  font-size: 17px;
}
.add-button {
  font-size: 24px;
  color: #5d5d5d;
  cursor: pointer;
}
.c-header.c-header-fixed {
  z-index: 1040;
}
.sidebar-top-margin {
  height: 55px;
}
.c-body {
  margin-top: 55px;
}
.c-sidebar-nav-link {
  transition: none;
}
.c-sidebar-minimized.app-sidebar .c-sidebar-nav-link:hover,
.c-sidebar-minimized.app-sidebar .c-sidebar-nav-dropdown-toggle:hover,
.c-sidebar-minimized.app-sidebar .c-sidebar-nav-dropdown-toggle:hover {
  width: 100%;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
